//
// _card.scss
//

.card {
  margin-bottom: $grid-gutter-width;
  // border-radius: 10px;
  box-shadow: $box-shadow;
  border-radius: $border-radius-mu;
  overflow: hidden;
}

.card-animate {
  transition: all .4s;
  -webkit-transition: all .4s;
}

.card-animate:hover {
  box-shadow: $box-shadow;
  transform: translateY(calc(-1.5rem / 5));
}

.card-drop {
  color: $body-color;
}

.card-title {
  font-size: 15.4px;
  margin: 0 0 7px 0;
  color: #F89622;
  font-weight: 600;
}

.card-title-desc,
.card-subtitle {
  color: $card-title-desc;
  margin-bottom: 0;
  font-size: 13px;
}

.card-header-tabs {
  margin-top: -$card-cap-padding-y;
}

.card-header-pills {
  margin: -(calc($card-cap-padding-y / 2));
}

body[data-layout-mode="dark"] {

  .card,
  .card-header,
  .modal-content,
  .offcanvas {
    background-color: $gray-dark-200;
    border-color: lighten($gray-dark-200, 4%);
  }

  .card-title-desc {
    color: $gray-dark-400;
  }
}