
.col-card-same-height{
    .col {
        display: table-cell;
        .card {
            height: 100%;
        }
    }
}

.note-row{
    border-bottom: .5px dashed rgba(0,0,0,.2);
}

body[data-layout-mode="dark"] {
    .circle-stat { 
      border: 4px solid rgba(255, 255, 255, 0.1);
    }
  }

.circle-stat {
    font-size: 1rem;
    font-weight: 700;
    border-radius: 50%;
    border: 4px solid rgba(204, 204, 204, 1);
    width: 65px;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0.75rem;
    margin: 0.5rem;
}

.countries-map {
    svg {
        touch-action: none;
      }
      .jvectormap-container {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        touch-action: none;
      }
      .jvectormap-tip {
        position: absolute;
        display: none;
        border: solid 1px #cdcdcd;
        border-radius: 3px;
        background: #292929;
        color: white;
        font-family: sans-serif, Verdana;
        font-size: smaller;
        padding: 3px;
      }
      .jvectormap-zoomin {
        position: absolute;
        left: 10px;
        border-radius: 3px;
        background: #292929;
        padding: 3px;
        color: white;
        cursor: pointer;
        line-height: 10px;
        text-align: center;
        box-sizing: content-box;
        width: 10px;
        height: 10px;
        top: 10px;
      }
      .jvectormap-zoomout {
        position: absolute;
        left: 10px;
        border-radius: 3px;
        background: #292929;
        padding: 3px;
        color: white;
        cursor: pointer;
        line-height: 10px;
        text-align: center;
        box-sizing: content-box;
        width: 10px;
        height: 10px;
        top: 30px;
      }
      .jvectormap-goback {
        position: absolute;
        left: 10px;
        border-radius: 3px;
        background: #292929;
        padding: 3px;
        color: white;
        cursor: pointer;
        line-height: 10px;
        text-align: center;
        box-sizing: content-box;
        bottom: 10px;
        z-index: 1000;
        padding: 6px;
      }
      .jvectormap-spinner {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: center no-repeat
          url(data:image/gif;base64,);
      }
      .jvectormap-legend-title {
        font-weight: bold;
        font-size: 14px;
        text-align: center;
      }
      .jvectormap-legend-cnt {
        position: absolute;
      }
      .jvectormap-legend-cnt-h {
        bottom: 0;
        right: 0;
        .jvectormap-legend {
          float: left;
          margin: 0 10px 10px 0;
          padding: 3px 3px 1px 3px;
          .jvectormap-legend-tick {
            float: left;
          }
        }
        .jvectormap-legend-tick {
          width: 40px;
        }
        .jvectormap-legend-tick-sample {
          height: 15px;
        }
        .jvectormap-legend-tick-text {
          text-align: center;
        }
      }
      .jvectormap-legend-cnt-v {
        top: 0;
        right: 0;
        .jvectormap-legend {
          margin: 10px 10px 0 0;
          padding: 3px;
        }
        .jvectormap-legend-tick-sample {
          height: 20px;
          width: 20px;
          display: inline-block;
          vertical-align: middle;
        }
        .jvectormap-legend-tick-text {
          display: inline-block;
          vertical-align: middle;
          line-height: 20px;
          padding-left: 3px;
        }
      }
      .jvectormap-legend {
        background: black;
        color: white;
        border-radius: 3px;
      }
      .jvectormap-legend-tick-text {
        font-size: 12px;
      }
      
}

.cursor-pointer {
  cursor: pointer;
}